@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@600&family=Source+Sans+Pro:wght@600;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  background-color: #eeeeee;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #ea7afd;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.Landing-Container {
  position: absolute;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
}

.Auth-Form {
  display: flex;
  flex-direction: column;
  height: 100vh;
  justify-content: center;
  width: 350px;
  margin: auto;
}

.Auth-Form form {
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
}

.Auth-Form h4 {
  text-align: center;
  font-size: 30px;
  margin: 15px 0px;
}

.Auth-Form form input {
  padding-left: 12px;
  height: 45px;
  border-radius: 5px;
  border: none;
  outline: 0.5px solid #bebebe;
}

.Auth-Form form input:hover,
.Auth-Form form input:focus {
  outline: 1px solid #b90000;
  transition: 0.5s ease-in-out;
}

.Backend-Errors {
  color: red;
}

.Global-Error {
  text-align: center;
}

.Auth-Form form button {
  height: 45px;
  width: 275px;
  border-radius: 25px;
  border: none;
  outline: 0.5px solid #bebebe;
  background: #b90000;
  background: linear-gradient(to right, #b90000 0%, #961f1f 51%, #7c2822 100%);
  color: white;
}

.Auth-Form form button:hover {
  filter: brightness(1.2);
  transition: 0.5s ease-in-out;
  outline: 1px solid rgb(138, 116, 116);
}

.Auth-Button-Group {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 12px;
  gap: 12px;
  margin-top: 10px;
}

.Auth-Input:-ms-input-placeholder {
  color: red;
}

.Auth-Input::placeholder {
  color: red;
}

button:hover,
a:hover,
li:hover {
  cursor: pointer;
}

h4,
label {
  font-family: "Roboto Mono", monospace;
  font-weight: 900;
}

button,
input {
  font-family: "Roboto Mono", monospace;
  font-weight: 400;
}

.Heading-Container {
  background-color: #fafafa;
  /* height: 7vh; */
  outline: 1px solid #e2e2e2;
  display: flex;
  justify-content: space-between;
  padding: 20px 25px;
}

.Heading-Container:first-child {
  font-size: 25px;
}

.Button-Group {
  display: flex;
  justify-content: space-between;
  grid-gap: 25px;
  gap: 25px;
}

.Heading-Button:hover {
  cursor: pointer;
  background-color: rgba(95, 0, 0, 0.384);
  transition: 0.25s ease-in;
}

.Heading-Button {
  outline: 1px solid #e2e2e2;
  border-radius: 25px;
  padding: 10px;
  background-color: #b9000070;
}

.Dashboard-Container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.Credential-Header {
  text-align: center;
}

.Credential-Form-Container {
  position: absolute;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  width: 400px;
  padding: 30px;
  background-color: #d3d3d3f3;
  border-radius: 5px;
  z-index: 9999;
}

.Credential-Form {
  display: flex;
  flex-direction: column;
  grid-gap: 15px;
  gap: 15px;
}

.Credential-Form input {
  height: 50px;
  padding-left: 12px;
  border-radius: 10px;
  outline: 1px solid grey;
  border: none;
  font-size: 14px;
}

.close:hover {
  cursor: pointer;
  opacity: 0.75;
}

.Credential-Form button {
  height: 40px;
  width: 80%;
  align-self: center;
  border-radius: 10px;
  border: none;
  outline: 0.5px solid grey;
}

button:hover {
  opacity: 0.6;
}

.Clipboard {
  position: absolute;
  margin: auto;
  left: 85%;
}

.Emoji-List {
  display: flex;
  list-style: none;
  justify-content: space-around;
  padding: 0;
}

.Emoji-List li {
  font-size: 35px;
}

.Emoji-List li:hover {
  cursor: pointer;
  filter: brightness(0.6);
}

.Clipboard:hover {
  cursor: pointer;
  opacity: 0.25;
}

.Copied {
  color: green;
}

.Darken-Input {
  background-color: #00e1ff1f;
}

.Dashboard-Map {
  width: 65vw;
  background-color: #e3e3e366;
  margin-top: 12px;
  border-radius: 5px;
  min-width: 1250px;
}

.Dashboard-Title {
  display: flex;
  justify-content: space-evenly;
}

.Cred-Row {
  display: flex;
}

h3 {
  text-align: center;
}

h4 {
  font-size: 25px;
  margin: 15px;
}

input:hover {
  opacity: 0.8;
}

.Cred-List {
  padding: 0px;
  min-height: 28px;
}

.Cred-List-Input {
  list-style: none;
  padding: 0px;
  text-align: left;
  margin-bottom: 15px;
}

.Cred-Button {
  padding: 0px 40px;
  border: none;
  outline: 0.5px solid rgb(206, 206, 206);
  outline-style: dashed;
}

/* #Removed {
} */

.Class li {
  text-align: center !important;
}

.Dashboard-Buttons {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
}

.Dashboard-Select {
  background-color: #b9000070;
  border-radius: 5px;
  padding-left: 5px;
}

/* Spinner */
.lds-ring {
  display: inline-block;
  position: absolute;
  width: 80px;
  height: 80px;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  top: 40vh;
}

.lds-ring div {
  box-sizing: border-box;
  display: block;
  position: absolute;
  width: 64px;
  height: 64px;
  margin: 8px;
  border: 8px solid #b90000;
  border-radius: 50%;
  animation: lds-ring 1.2s cubic-bezier(0.5, 0, 0.5, 1) infinite;
  border-color: #b90000 transparent transparent transparent;
}

.lds-ring div:nth-child(1) {
  animation-delay: -0.45s;
}
.lds-ring div:nth-child(2) {
  animation-delay: -0.3s;
}
.lds-ring div:nth-child(3) {
  animation-delay: -0.15s;
}

@keyframes lds-ring {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

